    /**
     * 
     * @param success 
     * @param fail 
     */
    public login(appid:string,secret:string,success:Function,fail:Function)
    {

        if(!this.wx)
        {
            success();
            return;
        }

        this.appid = appid;
        this.secret = secret;

        if(this.isLogin)
        {
            success();
            return;
        }


        var successCallback = ()=>{
            this.isLogin = true;
            WXSdk.instance.wx.hideLoading();
            success();
        };

        var failCallback = ()=>{
            WXSdk.instance.wx.hideLoading();
            fail();
        }


        this.applyUserAuth((isAuthorize)=>
        {
            this.getUserInfo(()=>
            {
                //this.checkSession(success,fail);
                this.loginWX(successCallback,failCallback);
            },()=>
            {
                failCallback();
            });

        });
    }


    public loginWX(success:Function,fail:Function)
    {
        this.wx.showLoading({
            title: '...',
            mask:true,
            width:250,
        });

        this.wx.login({
            timeout:30000,// ms
            success:(res)=>{
                this.wxLog("login",res);
                if(res.code)
                {
                    //console.log("code:"+res.code)
                    //this.getAccessToken(success,fail,res.code);
                    //this.getAccessToken2(success,fail,res.code);

                    success();

                }else
                {
                    //this.wxLog("",res);
                    fail();
                }
            },
            fail : (res)=>{
                //this.wxLog("",res);
                fail();
            },
            complete(){
                //
            }
        });
    }

