    public unloadUserScore(score:number,success:Function,fail:Function)
    {

        if(!this.wx)
            return;

        let url = this.worldRankUrl + `?id=${this.uid}&score=${score}`; //

        this.requestGet(url,
            (res:any)=>{
                
                this.wxLog("",res.data);

                if (res.data.code == 0) 
                {
                    success();
                }
                else
                {
                    fail();
                }
            },
            ()=>
            {
                console.error("");
                fail();
            }
        );
    }

    public getUserScoreWorldRank(success:Function,fail:Function)
    {

        if(!this.wx)
            return;

        let url = this.worldRankUrl2 + this.uid; //

        this.requestGet(url,
            (res:any)=>{
                
                this.wxLog("",res);

                if (res.data.code == 0) 
                {
                    success(res.data.data);
                }
                else
                {
                    fail();
                }
            },
            ()=>
            {
                console.error("");
                fail();
            }
        );

    }

